.media {
	@include container;

	background-color: var(--color-grey-100);

	&__items {
		display: flex;
		flex-wrap: wrap;
		margin-right: -15px;
		row-gap: 15px;

		@include mq(sm) {
			margin-right: -20px;
			row-gap: 20px;
		}
	}

	&__item {
		flex: 0 1 25%;
		padding-right: 15px;
		aspect-ratio: 2 / 1;

		@include mq(sm) {
			flex-basis: 50%;
			padding-right: 20px;
		}
	}

	&__link {
		@include flex-alignment(0);

		border: 1px solid transparent;
		padding: 12px;
		width: 100%;
		height: 100%;
		background-color: var(--color-white);
		transition:
			background-color 0.3s,
			border-color 0.3s,
			box-shadow 0.3s;

		&:focus-visible {
			@include focus-visible;
		}

		@media (any-hover: hover) {
			&:hover {
				border-color: var(--color-grey-400);
				background-color: transparent;
			}
		}
	}

	&__img {
		width: 100%;
		height: 30px;
		max-width: 156px;
		object-fit: contain;

		@include mq(sm) {
			width: 80px;
			height: 24px;
		}
	}
}
