@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter_Regular.woff2") format("woff2"), url("../fonts/Inter_Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter_Medium.woff2") format("woff2"), url("../fonts/Inter_Medium.woff") format("woff");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

:root {
  --color-white: #fff;
  --color-orange-400: #fa523d;
  --color-orange-600: #dc341f;
  --color-grey-100: #f1f1f1;
  --color-grey-200: #ddd;
  --color-grey-300: #dcdcdc;
  --color-grey-400: #b1b1b1;
  --color-grey-500: #989898;
  --color-grey-600: #6d6d6d;
  --color-dark-600: #1d1d1d;
  --color-black: #000;
  --rgba-white-200: rgba(255, 255, 255, 0.2);
  --rgba-white-500: rgba(255, 255, 255, 0.5);
  --rgba-black-500: rgba(0, 0, 0, 0.5);
  --rgba-orange-800: rgba(250, 82, 61, 0.8);
  --box-shadow-orange: 0 0 0 2px rgba(250, 82, 61, 0.8);
  --font-main: "Inter";
}

html {
  height: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

ul,
ol {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

ul {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font: inherit;
}

img {
  max-width: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
  color: inherit;
}

a {
  text-decoration: none;
  color: inherit;
}

address {
  font-style: normal;
}

button,
input,
textarea {
  display: inline-block;
  border: 0;
  padding: 0;
  background-color: transparent;
}

button {
  appearance: none;
  cursor: pointer;
}

input {
  margin: 0;
}

fieldset {
  margin: 0;
  border: 0;
  padding: 0;
}

legend {
  padding: 0;
}

picture {
  line-height: 0;
}

label {
  cursor: pointer;
}

.graph-modal {
  --transition-time: 0.3s;
  position: fixed;
  inset: 0;
  z-index: 1000;
  overflow: hidden auto;
  text-align: center;
  background-color: var(--color-white);
  opacity: 0;
  visibility: hidden;
  transition: opacity var(--transition-time), visibility var(--transition-time);
  cursor: pointer;
}

.graph-modal__container {
  position: relative;
  display: none;
  justify-content: center;
  vertical-align: middle;
  border-radius: 10px;
  width: 100%;
  height: 100%;
  text-align: left;
  background-color: var(--color-white);
  cursor: default;
}

.graph-modal__close {
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  position: absolute;
  right: 100px;
  top: 30px;
  outline: 0;
  border: 1px solid var(--color-grey-200);
  border-radius: 1000px;
  width: 52px;
  height: 39px;
  background-image: url("../img/sprite.svg#close");
  background-size: 16px;
  transition: background-color 0.3s, border-color 0.3s;
}

.graph-modal__close:focus-visible {
  border-color: var(--color-orange-400);
}

.graph-modal__box {
  margin: auto;
  padding: 20px;
  width: 100%;
  max-width: 620px;
}

.graph-modal .form {
  align-items: center;
}

.graph-modal .form__header {
  margin: 0 auto;
  width: 100%;
  max-width: 367px;
  text-align: center;
}

.graph-modal .form__title {
  display: inline-block;
  font-weight: 500;
  font-size: 30px;
  letter-spacing: -0.05em;
  color: var(--color-dark-600);
  line-height: 1.1;
}

.graph-modal .form__bottom {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 12px;
  width: 100%;
  max-width: 258px;
  text-align: center;
  gap: 12px;
}

.graph-modal.is-open {
  opacity: 1;
  visibility: visible;
  transition: opacity var(--transition-time), visibility var(--transition-time);
}

.graph-modal__container.graph-modal-open {
  display: flex;
}

.fade {
  opacity: 0;
}

.fade,
.fade.animate-open {
  transition: opacity var(--transition-time);
}

.fade.animate-open {
  opacity: 1;
}

.fadeInUp {
  opacity: 0;
  transform: translateY(-100px);
}

.fadeInUp,
.fadeInUp.animate-open {
  transition: transform var(--transition-time), opacity var(--transition-time);
}

.fadeInUp.animate-open {
  opacity: 1;
  transform: translateY(0);
}

.tabs__panel {
  display: none;
}

.tabs__nav {
  list-style: none;
  display: flex;
}

.tabs__panel--active {
  display: block;
}

.tabs__nav-btn:focus {
  color: #fff;
  background-color: navy;
}

.tabs__nav-btn--active {
  background-color: wheat;
}

