    background-color: var(--color-white);
  }

  .side-menu__items--dots[data-color=dark] .side-menu__link:hover {
    color: var(--color-dark-600);
  }

  .side-menu__items--dots[data-color=dark] .side-menu__link:hover::before {
    background-color: var(--color-orange-400);
  }

  .side-menu__items--arrows .side-menu__link:hover {
    border-bottom-color: var(--color-dark-600);
  }

  .side-menu__items--arrows[data-color=light] .side-menu__link:hover {
    border-bottom-color: var(--color-white);
  }

  .socials__link:hover {
    background-color: var(--color-orange-400);
  }

  .breadcrumbs__link:hover::before {
    background-color: var(--color-dark-600);
  }

  .footer__link:hover {
    border-bottom-color: var(--color-dark-600);
  }

  .product-card__image:hover::before {
    opacity: 0.2;
  }

  .product-card__title:hover,
  .product-card__question:hover {
    color: var(--color-orange-400);
  }

  .product-card__question:hover svg {
    color: var(--color-dark-600);
  }

  .product-card__question span:hover {
    border-bottom-color: var(--color-dark-600);
  }

  .product-card__question:hover {
    color: var(--color-dark-600);
  }

  .product-card__question:hover span {
    border-bottom-color: var(--color-grey-200);
  }

  .review-card:hover {
    border-color: var(--color-grey-400);
    background-color: transparent;
  }

  .blog-card:hover {
    background-color: var(--color-grey-100);
  }

  .facade-card__image:hover::after {
    opacity: 0.2;
  }

  .characteristic-card__value:hover svg {
    color: var(--color-dark-600);
  }

  .solution-card__image:hover::before {
    opacity: 0.8;
  }

  .solution-card__link:hover::before {
    opacity: 0.8;
  }

  .media__link:hover {
    border-color: var(--color-grey-400);
    background-color: transparent;
  }

  .filters__hidden:hover + .filters__visible {
    border-color: var(--color-dark-600);
  }

  .catalog__more:hover span {
    border-bottom-color: var(--color-dark-600);
  }

  .catalog__more:hover svg {
    color: var(--color-orange-400);
  }

  .catalog__more span:hover {
    border-bottom-color: var(--color-dark-600);
  }

  .product-about__question:hover svg {
    color: var(--color-dark-600);
  }

  .product-about__question span:hover {
    border-bottom-color: var(--color-dark-600);
  }

  .product-about__detail:hover {
    border-bottom-color: var(--color-dark-600);
  }

  .product-about__detail:hover {
    border-bottom-color: var(--color-grey-200);
  }

  .product-about .swiper-pagination-bullet:hover {
    border-color: var(--color-grey-500);
    background-color: transparent;
  }

  .product-about .swiper-pagination-bullet:hover::before {
    background-color: var(--color-dark-600);
    opacity: 1;
  }

  .quality-materials__button:hover {
    background-color: var(--color-orange-600);
  }
}

@media (hover: hover) {
  .f-button:hover:not([disabled]) {
    color: var(--f-button-hover-color);
    background-color: var(--f-button-hover-bg);
  }
}